import React from 'react';
import { Link } from "react-router-dom";
import logo from '../images/logo35.png';
import '../styles/Header.css';
let origin = window.origin.toString();

class Header extends React.Component {

    state = {

    };

    componentDidMount () {
        let pathname = window.location.pathname;
        let elem = document.getElementsByClassName(pathname.replace('/',''))[0];
        if(elem !== undefined) elem.click();
    }

    toggleClass = (e) => {
        //e.preventDefault();
        let otherElems = document.querySelectorAll('.header__elem1__link');
        otherElems.forEach( elem => elem.classList.remove('--selected'));
        let elem = e.currentTarget.classList;
        elem.add('--selected');
    };

    cleanConsole = () => {
        console.clear();
    };

    render () {

        let simbol;
        simbol = origin.includes('localhost') ? '🐋️' : '🐴';

        return(
            <div className={"header"}>
                <div className={"header__elem1"}>
                    <div className={"header__elem1__logo"}> <img src={logo} width={35} height={35}/></div>
                    <Link to="/dimensionpkg2022" className={"header__elem1__link quick dimensionpkg2022"} onClick={this.toggleClass}> 차여패 </Link>
                    <div className={"nav-vertical-bar"}>  </div>
                    <Link to="/rareAvatar" className={"header__elem1__link quick rareAvatar"} onClick={this.toggleClass}> 레압 </Link>
                    <Link to="/emblem" className={"header__elem1__link quick emblem"} onClick={this.toggleClass}> 엠블렘 </Link>
                    <Link to="/test" className={"header__elem1__link quick test"} onClick={this.toggleClass}> 테스트 </Link>
                    {/*<Link to="/awakenpkg2022" className={"header__elem1__link quick awakenpkg2022"} onClick={this.toggleClass}> 전패 </Link>*/}
                    {/*<Link to="/revelation2021" className={"header__elem1__link quick revelation2021"} onClick={this.toggleClass}> 신계 </Link>*/}
                    {/*<Link to="/midsummerpkg2021" className={"header__elem1__link quick midsummerpkg2021"} onClick={this.toggleClass}> 미아 </Link>*/}
                    {/*<Link to="/about" className={"header__elem1__link quick about"} onClick={this.toggleClass}> 정보 </Link>*/}
                </div>
                <div className={"header__elem2 about"}>
                    <Link to="/about" onClick={this.cleanConsole}> { simbol } </Link>
                </div>
            </div>
            )
    }
}

export default Header;