function setDevServerFavicon() {
    return new Promise( function( resolve, reject ) {
        var link = document.createElement( 'link' );
        link.rel  = 'shortcut icon';
        link.href = devFavicon;
        document.head.appendChild( link );
        link.onload = function() { resolve(); };
    } );
}
function LoadCSS( cssURL ) {
    return new Promise( function( resolve, reject ) {
        var link = document.createElement( 'link' );
        link.rel  = 'stylesheet';
        link.href = cssURL;
        link.integrity = 'sha384-UHRtZLI+pbxtHCWp1t77Bi1L4ZtiqrqD80Kn4Z8NTSRyMA2Fd33n5dQ8lWUE00s/';
        link.crossOrigin = 'anonymous';
        document.head.appendChild( link );
        link.onload = function() { resolve(); };
    } );
}

if(window.location.origin.includes('localhost')) setDevServerFavicon();
LoadCSS('https://use.fontawesome.com/releases/v5.6.3/css/all.css');
LoadCSS('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@100;200;300;400;500&display=swap');

