import React from 'react';
import neople from '../images/neople.png';
import '../styles/Footer.css';

class Footer extends React.Component {

    openAsPopup () {
        window.open(document.URL, '','width=341,height=480');
    }
    render () {
        return (<div id={"footer"}>
            <div className={"honey-bee"}></div>
            <div></div>
            <div className={"openPopup"} onClick={this.openAsPopup}>open as popup</div>
            <a href={'https://developers.neople.co.kr/contents/apiDocs/df'}>Powered by Neople OpenAPI</a>
            <div> <span role={"img"} className={"footer-div"}>⚡</span> </div>
            <img className={"footer-img"} src={neople} href="https://developers.neople.co.kr/contents/apiDocs/df"></img>
        </div>)
    }

}

export default Footer;