import React from 'react';
import Modal from 'react-modal';
import socketIO from 'socket.io-client'
import '../styles/Quick.css';
import notiSound_1 from '../sounds/noti_1.mp3';
import notiSound_2 from '../sounds/noti_2.mp3';
import itemIconPlaceholder from '../images/itemIconPlaceholder.png';

function AddComma(param, nvl) {
    // add NVL
    if (param === 0 && nvl !== undefined) return nvl;
    return Number(param).toLocaleString('en');
}

class RealtimeSearch extends React.Component {

    state = {
        //DATA
        snapshot : {},
        prevSnapshot : {},

        summary : {},
        prevSummary : {},

        cuttline : 6700000,
        timeStamp : 0,

        //CSS OPTION
        showAttributeRow : false,
        downloadProgress : '--wait',
        innerWidth : window.innerWidth,
        modalisOpen : false,

        //COMP OPTION
        tempAlramVar : 0,

        //
        messageBox : []
    };

    socket = '';
    console_title = 'title_placeholder';

    componentDidMount () {
        this.console_title = [`%c@${this.props.TITLE}`, 'background: #222; color: #bada55'];
        console.log(...this.console_title, ` MOUNT`);
        //🔌
        this.toTTS(" "); // avoiding speech not assigned voice when first time
        window.addEventListener("resize",  () => {
            this.setState({innerWidth: window.innerWidth});
        }, false);

        if ( localStorage[this.props.TITLE] === undefined ) localStorage[this.props.TITLE] = 100000;
        else this.setState({cuttline : localStorage[this.props.TITLE]});

        const temp_url = window.location.origin.split(':')[1];
        this.socket = socketIO.connect(temp_url, {transports: ['websocket']}, {'reconnectionAttempts': 5});
        const socket = this.socket;

        //console.log(socket.connected);

        socket.on('connect',  () => {
            console.log(...this.console_title, ` CONNECT`);
        });

        // TODO:SOCKET CONNECT() IS ASYNC FUNCTION. IF BELOW CODE IS REACHED WHEN BEFORE CONNECT IS ESTABILISHED
        // TODO:SOCKET IO INSERT THIS EMIT() IN QUEUE AND RUN AFTER ESTABILISHED?
        socket.emit(`request_data_from_client-${this.props.TITLE}`, '');

        socket.on('connect_failed', () => {
            console.log(...this.console_title, ` CONNECT FAIL`)
        });
        socket.on('reconnect_failed', () => {
            console.log(...this.console_title, ` RECONNECT FAIL`)
        });

        socket.on(`snapshot-${this.props.TITLE}`, async (received_snapshot, timeStamp) => {
            let delayTime = async (time) => await new Promise(res => setTimeout(res, time));
            if(!window.location.origin.includes('localhost') && Object.keys(this.state.prevSnapshot).length !== 0) {
                console.log('not localhost');
                await delayTime(10);
            }
            // cssing
            let timeDiff = Math.abs(timeStamp - new Date().valueOf());
            if(timeDiff > 180000) {
                this.setState({downloadProgress : '--receiveOldData'});
                this.pushMessage(`마지막 데이터를 수신한 지 3분 이상 경과하였습니다`, 5000, '⚠️');
            } else {
                this.setState({downloadProgress : '--downloadComplete'}, () => {setTimeout(()=> this.setState({downloadProgress:'--wait'}), 2000)});
            }

            let snapshot = received_snapshot;

            let prevSnapshot = this.state.snapshot;
            let prevSummary = this.state.summary;
            let hash = this.hashCode(JSON.stringify(snapshot));
            let isPrevSnapshotExist = Object.keys(this.state.prevSnapshot).length !== 0;

            // pre-processing #1 : re-sorting (for merged columss)
            Object.keys(snapshot).forEach((elem, idx) => {
                if (Array.isArray(snapshot[elem]) === true)
                    snapshot[elem].sort((a, b) =>  a.unitPrice - b.unitPrice);
            });
            //
            let lowest = Object.keys(snapshot).reduce((acc, cuv) => {
                if(snapshot[cuv].length === 0) acc[cuv] = 0;
                else acc[cuv] = snapshot[cuv][0].unitPrice;
                return acc;
            }, {});

            let quantity = Object.keys(snapshot).reduce((acc, itemName) => {
                acc[itemName] = snapshot[itemName].reduce((quan, cuv) => {
                    quan += cuv.count;
                    return quan;
                },0);
                return acc;
            },{});

            let summary = {
                lowest : lowest,
                quantity : quantity,
                hash : hash
            };

            if(!isPrevSnapshotExist) {
                prevSnapshot = snapshot;
                prevSummary = summary;
            }
            this.setState({
                snapshot : snapshot,
                prevSnapshot: prevSnapshot,

                summary : summary,
                prevSummary : prevSummary,

                timeStamp : timeStamp,
                hash : hash
            }, this.notiCallback);
        });
    }

    componentWillUnmount = () => {
        this.console_title = [`%c@${this.props.TITLE}`, 'background: #222; color: #bada55'];
        console.log(...this.console_title, ` UNMOUNT`);
        this.socket.disconnect();
    };

    toTTS = (utterance) => {
        var synth = window.speechSynthesis;
        let voice_KR = synth.getVoices()[12];
        var utterThis = new SpeechSynthesisUtterance(utterance);
        utterThis.voice = voice_KR;
        utterThis.volume = 0.5;
        synth.speak(utterThis);
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(Object.keys(this.state.snapshot).length === 0) return;
        let viewerType = this.props.OPTION.type;
        let viewerTarget = this.props.OPTION.target;
        let isNotify = false;

        if( viewerType === 'package') {

            let lowest = this.state.summary.lowest;
            let packagePrice = 1*(lowest[viewerTarget.package]);
            let packageContentsPrice = (this.calculatePackage(lowest, viewerTarget.packageContents));
            let packageAvatarBoxPrice;
            let calculatedPrice = 1*packagePrice - 0.97*packageContentsPrice;

            // set Color packageAvatarBox-Row and additionalRow
            if (this.isOverCuttLine(calculatedPrice)) document.querySelector('[data-itemname=additionalRow]').classList.add('--red');
            else document.querySelector('[data-itemname=additionalRow]').classList.remove('--red');

            if(viewerTarget.packageAvatarBox !== undefined) {
                packageAvatarBoxPrice = lowest[viewerTarget.packageAvatarBox].price;
                if (this.isOverCuttLine(packageAvatarBoxPrice)) document.querySelector(`[data-itemname=${viewerTarget.packageAvatarBox}]`).classList.add('--red');
                else document.querySelector(`[data-itemname=${viewerTarget.packageAvatarBox}]`).classList.remove('--red');
            }
            // when getting new snapshot, compare targets
            if (prevState.timeStamp !== this.state.timeStamp && this.state.snapshot[viewerTarget.package].length !== 0 && this.state.prevSnapshot[viewerTarget.package].length !== 0) {

                let isSamePackage = true;
                let isSamePackageAvatarBox = true;
                console.log(this.state.snapshot);
                isSamePackage = this.state.snapshot[viewerTarget.package][0].auctionNo === this.state.prevSnapshot[viewerTarget.package][0].auctionNo;
                if (viewerTarget.packageAvatarBox !== undefined)
                    isSamePackageAvatarBox = this.state.snapshot[viewerTarget.packageAvatarBox][0].auctionNo === this.state.prevSnapshot[viewerTarget.packageAvatarBox][0].auctionNo;

                if( (calculatedPrice <= this.state.cuttline) && !isSamePackage ) {
                    let itemName = this.state.snapshot[viewerTarget.package][0].itemName;
                    let price = lowest[viewerTarget.package];
                    this.toTTS(itemName + price);
                }
                if ( (packageAvatarBoxPrice <= this.state.cuttline) && !isSamePackageAvatarBox ) {
                    let itemName = this.state.snapshot[viewerTarget.packageAvatarBox][0].itemName;
                    let price = lowest[viewerTarget.packageAvatarBox];
                    this.toTTS(itemName + price);
                }

                isNotify = ((calculatedPrice <= this.state.cuttline) || (packageAvatarBoxPrice <= this.state.cuttline)) && (lowest[viewerTarget.package] !== 0) && (packageAvatarBoxPrice !== 0) && this.state.tempAlramVar === 0;
            }
        }
        if( viewerType === 'watch') {

        }
        if(isNotify) {
            console.log(...this.console_title, ` NOTIFICATION at ${ new Date().toLocaleString() }`);
            let callback = () => setTimeout(() => this.setState({tempAlramVar: 0}), 15000);
            this.setState({tempAlramVar: 1},callback);
            let audioEl = document.getElementsByClassName("audio-element")[(this.props.OPTION.notiSoundType || 0)];
            audioEl.volume = 0.7;
            audioEl.play();
        }
    };

    notiCallback = () => {
    };

    hashCode = (s) => {
        return s.split("").reduce(function(a,b){a=((a<<5)-a)+b.charCodeAt(0);return a&a},0);
    };

    hashConvert = (num) => {
        return String(Math.abs(num)).split('').map((elem)=> 1*elem).reduce((acc,cuv) => (acc += String.fromCharCode(97+cuv), acc),'');
    };

    priceDifference = (prev, curr) => {
        let diff = curr - prev;
        let sign = Math.sign(curr - prev);
        if(sign === 0)  return {class : '', value : '-' };
        if(sign === 1)  return {class : '--plus', value : Math.abs(diff).toLocaleString('en')};
        if(sign === -1) return {class : '--minus', value : Math.abs(diff).toLocaleString('en')};
    };

    isOverCuttLine = (price) => {
        return !!price === false ? false : Math.sign(price - this.state.cuttline) < 1;
    };

    watchToggle = () => {

    };

    inputVerify = (e) => {
        let numChuck = Number.isInteger(1*e.target.value);
        if(numChuck) {
            localStorage[this.props.TITLE] = 1*e.target.value;
            return this.setState({cuttline: Number(e.target.value)});
        }
    };

    calculatePackage = (lowest, packageContents) => {
        if(Object.keys(lowest).length === 0) return 0;
        if(!Array.isArray(packageContents) || this.props.OPTION.type !== 'package') return 0;
        let sumOfBody = packageContents.reduce((acc, itemName) => {
            if(lowest[itemName] !== undefined) acc += lowest[itemName];
            return acc;
        }, 0);
        return sumOfBody;
    };

    applyNotation = (itemObject) => {
        if( typeof itemObject === 'object') {
            let itemName = itemObject.itemName;
            if(itemObject.itemType === '방어구' || itemObject.itemType === '무기' || itemObject.itemType === '추가장비' || ( itemObject.itemType === '액세서리' && itemObject.itemTypeDetail !== '칭호'   ) ) itemName += ` [+${itemObject.reinforce}]`;
            if(itemObject.itemTypeDetail === '무기아바타') itemName += ` [${itemObject.jobName}]`;
            itemName = itemName.replace(new RegExp(this.props.OPTION.prefix, 'g'), '');
            if(itemName.includes('아바타 풀세트')) itemName = itemName.replace('풀세트 상자', '');
            return itemName;
        }
        else {
            let itemName = itemObject;
            itemName = itemName.replace(new RegExp(this.props.OPTION.prefix, 'g'), '');
            if(itemName.includes('아바타 풀세트')) itemName = itemName.replace('풀세트 상자', '');
            return itemName;
        }
    };

    downloadSnapshot = () => {
        var filename = `rawData__${this.props.TITLE}__${new Date().toLocaleString().replace(/:/g, '꞉')}.json`;
        var a = document.createElement('a');
        var blob = new Blob([JSON.stringify(this.state)]);
        a.download = filename;
        a.href = URL.createObjectURL(blob);
        a.click();
        URL.revokeObjectURL(blob);
    };

    pushMessage = (message, timeOut, icon) => {
        console.log(this.state.messageBox);
        this.setState({messageBox : [...this.state.messageBox, {icon : icon, message : message, timeStamp : Date.now(), timeOut : timeOut}]},
            () => {
                setTimeout( ()=> {
                    let m = this.state.messageBox;
                    m.splice(0,1);
                    this.setState({messageBox : m});}, timeOut);
            });
    };

    copy = (param) => {
        navigator.clipboard.readText().then((text) => {
        });
        let lowest = this.state.summary.lowest;
        let str = '';
        str += new Date(this.state.timeStamp).toLocaleString();
        str += '\t';
        let space = '\t';
        if(param === 'vertical') space = '\n';
        let len = Object.keys(lowest).length;
        Object.keys(lowest).forEach((elem, idx) => {
            if(idx !== len -1) str += lowest[elem] + space;
            else str += lowest[elem];
        });
        navigator.clipboard.writeText(str);
        this.pushMessage('복사되었습니다', 2000, '✔️');
    };

    keyEventHandler = (e) => {
        if(e.key === 'c' || e.key === 'C') { this.copy() }
        // ...
    };

    openModal = (itemName) => {
        this.setState({modalisOpen : itemName});
    }

    render() {
        let snapshot = this.state.snapshot;
        let lowest = this.state.summary.lowest || {};
        let prevLowest = this.state.prevSummary.lowest || {};
        let quantity = this.state.summary.quantity || {};

        let viewerTarget = this.props.OPTION.target;
        let viewerType = this.props.OPTION.type;

        let attributeRow;
        let dataRow;
        let emptyRow;
        let additionalRow;
        let underTable;
        let modal;
        let menuModal;

        //attributeRow
        attributeRow = <div className={"attribute row --shown-"+this.state.showAttributeRow} onClick={() => this.setState({shown: !this.state.showAttributeRow})}>
            <div className={"data cell --super-short"}> </div>
            <div className={"attribute cell itemname --long"}> 검색어 </div>
            <div className={"attribute cell --short"}> 가격 </div>
            <div className={"attribute cell --short"}> 변동폭 </div>

            {this.state.innerWidth >= 490 ? <div className={"attribute cell --extra-short"}> 물량 </div> : null}
        </div>;

        dataRow =  (itemName, idx) => {
            let price = lowest[itemName];
            let priceDifference = this.priceDifference(prevLowest[itemName], price);
            return(
                <div className={"data row"} data-itemname={itemName} key={idx} >
                    <div className={"data cell --super-short"}><img src={ snapshot[itemName][0] ? `https://img-api.neople.co.kr/df/items/${(snapshot[itemName][0] || {}).itemId}` : null }  /></div>
                    <div className={"data cell itemname --long --elipsis"} title={itemName}> { snapshot[itemName][0] !== undefined ? this.applyNotation(snapshot[itemName][0]) : itemName } </div>
                    <div className={"data cell --short --num --hover-bold --pointer"} onClick={() => this.openModal(itemName)}> <div className={"--hover-mint"}>{ AddComma(price, '×') }</div> </div>
                    <div className={"data cell --short --elipsis --num "+priceDifference.class}> {priceDifference.value} </div>
                    {this.state.innerWidth >= 490 ? <div className={"data cell --extra-short --num "}> { quantity[itemName] } </div> : null }
                </div>
            )
        };

        emptyRow = (idx) => {
            console.log(this.props.OPTION.title, idx);
            return(
                <div className={"data row"} key={idx} >
                    <div className={"data cell --super-short"}> <img src={itemIconPlaceholder} /></div>
                    <div className={"data cell itemname --long --elipsis"}>  </div>
                    <div className={"data cell --short --num --hover-bold --pointer"}> {"\u00A0"}  </div>
                    <div className={"data cell --short --elipsis --num "}> </div>
                    {this.state.innerWidth >= 490 ? <div className={"data cell --extra-short --num "}> </div> : null }
                </div>
            )
        };

        //addtionalRow, with calculating
        if(viewerType === 'package') {
            let packagePrice = 1*(lowest[viewerTarget.package]);
            let packageContentsPrice = (this.calculatePackage(lowest, viewerTarget.packageContents));
            let calculatedPrice = 1*packagePrice - 0.97*packageContentsPrice;

            additionalRow = <div className={"data row"} data-itemname={"additionalRow"}>
                <div className={"data cell --super-short"}> </div>
                <div className={"data cell itemname --long"}> { this.props.OPTION.additionalRowTag || "실제 아바타값" } </div>
                <div className={"data cell --short --num "} title={Math.floor(0.97 * packageContentsPrice )}> { Math.floor(calculatedPrice || 0).toLocaleString('en') || 0 } </div>
                <div className={"data cell --short"}> {  } </div>
                {this.state.innerWidth >= 490 ? <div className={"data cell --extra-short"}> {  } </div> : null }
            </div>;
        }
        else if (viewerType === 'none') {
            additionalRow = '';
        }

        let typeBoolean = viewerType === 'watch' || viewerType === 'package';
        let inputData = this.props.OPTION.type !== 'package' ? ' °˖✧◝(⁰▿⁰)◜✧˖°' : this.state.cuttline;
        underTable =
            <div className={"viewer-console --flex"}>
                <div className={"--flex --space-between"}>
                    <div className={"receive-time --flex"}>
                        <div className={"--pointer"} onClick={this.downloadSnapshot}>
                            <div><i className={"fas fa-caret-square-down --viewer-progress "+this.state.downloadProgress}></i></div>
                        </div>
                        <div> { new Date(this.state.timeStamp || 0).toLocaleTimeString() } </div>
                    </div>
                    <div onClick={this.copy.bind(this, 'horizon')} style={{cursor: "pointer"}}> { ' ' } </div>
                </div>
                <div className={"--flex --relative " + ( typeBoolean ? '' : '--unable')}>
                    { typeBoolean ? <i className="far fa-eye --inner-eye" onClick={this.watchToggle}></i> : <i className="far fa-eye-slash --inner-eye"></i>}
                    <input className={"--simpleInput"} id={`autoComplete2-`+this.props.TITLE} value={ inputData } onChange={this.inputVerify}/>
                </div>
            </div>;

        modal = <div>
            <Modal
                isOpen={!!this.state.modalisOpen}
                onRequestClose={() => this.setState({modalisOpen : false})}
                shouldReturnFocusAfterClose={false}
                appElement={document.getElementsByClassName('App')}
                style={{
                    overlay:{
                        'backgroundColor' : 'rgb(64 64 64 / 56%)',
                        'transition' : '3s'
                    },
                    content:{
                        '::WebkitScrollbar' : 'block',
                        'background' : 'whitesmoke',
                        'fontSize' : '18px',
                        'fontFamily' : 'Kopub M',
                        'color' : '#333',
                        left                  : '50%',
                        right                 : 'auto',
                        transform             : 'translate(-50%, -0%)',
                        'minWidth': '370px'
                    }
                }}
            >
                {!!this.state.modalisOpen ?
                    <div onClick={() => this.openModal(false)}>
                        {
                            this.state.snapshot[this.state.modalisOpen].map((item, idx) => {
                                return (<div className={"modal-row"} style={{"display" : "flex"}} key={idx}>
                                    <div className={"p1"} style={{"margin" : "0 8px 3px 0"}}>{ this.applyNotation(item) }</div>
                                    <div className={"p2"} style={{"margin" : "0 8px 3px 0"  }}>[{item.count}]</div>
                                    <div className={"p3"}> ||&nbsp;&nbsp;&nbsp;&nbsp;  {AddComma(item.unitPrice)}</div>
                                </div>)
                            })
                        }
                    </div>
                    : <div> </div>}
                {/*<button onClick={()=> this.setState({modalisOpen : false})}>x</button>*/}
                <div className={"modal-endline"}></div>
            </Modal></div>

        return (
            <div id={"quick"} className={this.props.OPTION.title}  tabIndex={0} onKeyPress={this.keyEventHandler}>
                <div className={"float-message-box"}>
                    {
                        this.state.messageBox.map((elem, idx) => {
                            return (
                                <div className={`floating-modal` } style={{'animation': `fadeout ${elem.timeOut/1000}s`,
                                    'animation-fill-mode': 'forwards'}} key={elem.timeStamp}><span style={{'color' : 'black'}}>{elem.icon}</span> {elem.message} </div>
                            )
                        })
                    }
                </div>

                {modal}

                <audio className={"audio-element"}>
                    <source src={notiSound_1}/>
                </audio>
                <audio className={"audio-element"}>
                    <source src={notiSound_2}/>
                </audio>
                <div className={"wrapper"}>
                    <div className={"auction-viewer"}>
                        {attributeRow}
                        {Object.keys(this.state.snapshot).length !== 0 ?
                            Object.keys(lowest).map((itemName, idx) => dataRow(itemName, idx)) : [1,2,3,4,5].map((e,idx) => emptyRow(idx))}
                        {additionalRow}
                        {underTable}
                    </div>
                    <div className={"notification-bar"}> </div>
                </div>
            </div>
        );
    }
}

RealtimeSearch.defaultProps  = { OPTION : {}};

export default RealtimeSearch;
